module.exports = "import React, {Component} from 'react';\nimport {Icon, Tooltip, Typography} from '@material-ui/core';\nimport GoogleMap from 'google-map-react';\n\nfunction Marker({text})\n{\n    return (\n        <Tooltip title={text} placement=\"top\">\n            <Icon className=\"text-red\">place</Icon>\n        </Tooltip>\n    );\n}\n\n\nclass SimpleExample extends Component {\n\n    render()\n    {\n        return (\n            <div className=\"w-full\">\n                <Typography className=\"h2 mb-16\">Simple Map Example</Typography>\n                <div className=\"w-full h-512\">\n                    <GoogleMap\n                        bootstrapURLKeys={{\n                            key: process.env.REACT_APP_MAP_KEY\n                        }}\n                        defaultZoom={12}\n                        defaultCenter={[-34.397, 150.64]}\n                    >\n                        <Marker\n                            text=\"Marker Text\"\n                            lat=\"-34.397\"\n                            lng=\"150.644\"\n                        />\n                    </GoogleMap>\n                </div>\n            </div>\n        );\n    }\n}\n\nexport default SimpleExample;\n"